import React from "react";
import "../styles/Carousel.css"
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Project, HardCodedProjects } from "../components/Project"

export default class ProjectCarousel extends React.Component<{homeSlide:"start" | "aboutMe", setCarouselState:any}, {mode: "default"|"description", slide: "start"|"aboutMe"|number}> {
    constructor(props: {homeSlide: "start" | "aboutMe", setCarouselState:any}, context: any) {
      super(props, context);
      this.state = {mode: "default", slide: this.props.homeSlide};
    }

    setMode(newMode: "default"|"description") {
      this.setState({mode: newMode})
    }

    setSlide(newSlide: "start"|"aboutMe"|number, newMode: "default"|"description") {
      this.setState({slide: newSlide})
      this.setMode(newMode)
    }

    setHomeSlide(newSlide: "start"|"aboutMe") {
      this.setState({slide: newSlide})
      this.props.setCarouselState(newSlide)
    }

    render() {

      let projects = HardCodedProjects

      return (
        <div>
          { (
            () => {
              if (this.state.slide == "start" && this.props.homeSlide == "start"){
                return (

                  <Carousel infiniteLoop useKeyboardArrows showThumbs = {false}>
                  
                  <div key = {"landing"} className="project-box">
                        
                        <img src= {require("../images/TemporaryPic.png")}/>

                        <p className="legend"/>
                          
                        <div className="project-textbox">
                          <div className="project-head">
                            <p className="project-title">Welcome to my portfolio page.</p>
                          </div>
                          <p className="project-text">
                            My name is Daniel and I believe I'm the quick-learning engineer you need for your next
                            tech project.
                            Click the button labelled "about me" for more about me.<br/>
                            <br/>
                            On this page you will find various projects I've worked on and what I've learned
                            while doing them.<br/>
                            <br/>
                            Please click the arrow on the right to browse through my portfolio chronologically
                            or go left to go straight to my latest project. You can also use the arrow keys to navigate.
                          </p>
                        </div>
                  </div>

                  {
                    projects.map (
                      (project, i) => 
                        <div key = {i} className="project-box">
                          
                          <img src= {project.image}/>
                          
                          <p className="legend">{project.name}</p>

                          <div className="project-textbox">
                            <div className="project-head">
                              <p className="project-title">{project.name}</p>
                              <p className="project-blurb">{project.blurb}</p>
                            </div>
                            <p className="project-learned" style = {{fontWeight : "bold"}}>Learned how to:</p>
                            {project.learned.map (
                              (thing, i) =>
                                <p key = {"learnedThing " + i} className="project-learned">{thing}</p>
                            )}

                          </div>

                          <div className ="project-cards">
                            <div className="card">
                              <div className="card-header">
                                Using:
                              </div>
                              <ul className="list-group list-group-flush">
                                {project.tools.map(
                                  (tool, i) =>
                                  <li key = {"tool" + i} className="list-group-item">{tool}</li>
                                )}
                              </ul>
                            </div>
                            <div className="card bottom-card">
                              <button className="button card-header"
                              onClick={()=>this.setSlide(i,"description")}>
                                <b>Read full description</b>
                              </button>
                            </div>
                          </div>
                        </div>
                    )
                  }
                  </Carousel>
                )
              }
            })()
          }
          { (
            () => {
              if (this.props.homeSlide == "aboutMe"){
                return (

                  <div className="project-box">
                    <div style = {{display : "flex", flexDirection: "column"}}>
                      <img src= {require("../images/TemporaryPic.png")} style = {{objectFit : "fill"}} className = "fullImage"/>
                      <div className="card">
                        <button className="button card-header return-button"
                        style = {{marginLeft : "5vmin"}}
                        onClick={()=>this.setHomeSlide("start")}>
                          <b>Return</b>
                        </button>
                      </div>
                    </div>
                    <p className="project-description">
                      {[
                        `
                        Hi! I'm Daniel. This isn't going to be some tale about how
                        I started programming at the age of eight. I've always been
                        a language and history kind of person growing up, not a math
                        person.
                        `,
                        `
                        I am however logical and analytical and enjoy creating things.
                        My introduction to Computer Science came with an online course I did
                        that involved building a functional (virtual) computer starting with
                        only a NAND logic gate. I was surprised at how enjoyable it was to me.
                        `,
                        `
                        Go forward a bunch of years and I drop both Cultural Anthropology and Linguistics
                        to study Computer Science instead. By this point I know six natural languages, so
                        learning programming languages did not pose a huge hurdle. With some luck I managed
                        to surround myself with the right people, the kind who did actually start programming
                        at the age of eight, and together we start working on all the programming projects
                        school throws at us.
                        `,
                        `
                        I have worked with many languages and frameworks since, and some of my experiences
                        are listed on this site. The takeaway though is that I am a quick and eager learner.
                        I like trying new things and figuring out how they work.
                        `,
                        `
                        Feel free to continue browsing the site! You can also go to my resume through
                        the CV link up top.
                        `,
                      ]
                      .map (
                        (paragraph, i) =>
                          <p key = {"descriptionParagraph " + i}>{paragraph}</p>
                      )}
                    </p>
                  </div>

                )
              }
            })()
          }
          { (
            () => {
            if (this.state.slide != "start" && this.state.slide !="aboutMe" && this.props.homeSlide != "aboutMe"){
              return(
                <div className="project-box" style = {{justifyContent: "center"}}>
                  <div style = {{display : "flex", flexDirection: "column"}}>
                    <img src= {projects[this.state.slide].image} style = {{objectFit : "fill"}} className = "fullImage"/>
                    <div className="card">
                      <button className="button card-header return-button"
                      style = {{marginLeft : "5vmin"}}
                      onClick={()=>this.setSlide("start","default")}>
                        <b>Return</b>
                      </button>
                    </div>
                  </div>
                  <div className="project-description">{
                    projects[this.state.slide].description.map (
                      (paragraph, i) =>
                        <p key = {"descriptionParagraph " + i}>{paragraph}</p>
                    )
                  }</div>
                </div>
              )
            }
          })()
        }
        </div>
      );

    }
  }