import React, { Component } from 'react';
//import logo from './logo.svg';
import './styles/App.css';
import { Menu } from "./components/Menu"
import ProjectCarousel from './components/ProjectCarousel';


class App extends Component<{},{carouselState: "start"|"aboutMe"}> {
  
  constructor(props: {}, context: any) {
    super(props, context);
    this.state = {carouselState: "start"}
  }

  setCarouselState = (newState: "start"|"aboutMe") => { 
    this.setState( {carouselState: newState} )
  }
  
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Menu setCarouselState = {this.setCarouselState}/>
          <ProjectCarousel homeSlide = {this.state.carouselState} setCarouselState = {this.setCarouselState}/>
        </header>
        <footer className="App-footer">
          © 2019 Daniel Dudink - v1.01 
        </footer>
      </div>
    );
  }
}

export default App;
