import React from "react";
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import NavDropdown from 'react-bootstrap/NavDropdown'
import "../styles/Menu.css" 

export class Menu extends React.Component<{setCarouselState: any}> {
  render() {
    return (

      <Navbar collapseOnSelect expand="lg">
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto" variant="pills">
            <Nav.Link href="/dgdudink">Home</Nav.Link>
            <Nav.Link onClick={()=>this.props.setCarouselState("aboutMe")}>About Me</Nav.Link>
          </Nav>
          <Nav variant="pills">
            <NavDropdown title="Links" id="collapsible-nav-dropdown">
              <NavDropdown.Item href="https://www.linkedin.com/in/daniel-d-2a150b187/">
                  <img src = {require("../images/LinkedInLogo.png")} height = "30vmin" width = "30vmin" title = "LinkedIn"/>
              </NavDropdown.Item>
              <NavDropdown.Item href = "https://docs.google.com/document/d/1cRkqMQlmXVA0WCSjvflxUT6KMXW0tqPuJ6vI9RSjFBU/edit?usp=sharing">
                  <b>CV</b>
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}